import "./App.css";
import { useEffect } from "react";
import LoginPage from "./components/LoginPage";
import Pending from "./components/Pending";
import Navigation from "./components/Navigation";
import InfoCard from "./components/InfoCard";
import FilterButtons from "./components/FilterButtons";
import PieChart from "./components/PieChart";
import GroupedBar from "./components/GroupedBar";
import LineChart from "./components/LineChart";
import ServerAlert from "./components/ServerAlert";
import ErrorPage from "./components/ErrorPage";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import { useSelector, useDispatch } from "react-redux";
import { fetchData } from "./redux/reducer";

const useStyles = makeStyles({
  root: {
    width: "100%",
    maxWidth: "600px",
    marginLeft: "auto",
    marginRight: "auto",
  },
  block: {
    marginTop: "1.5em",
  },
  standout: {
    marginTop: "1.5em",
    marginBottom: "1em",
  },
});

function App() {
  const classes = useStyles();

  const pending = useSelector((state) => state.app.pending);
  const data = useSelector((state) => state.app.data);
  const isLoggedIn = useSelector((state) => state.app.isLoggedIn);
  const serverError = useSelector((state) => state.app.serverError);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchData());
  }, [dispatch]);

  if (pending) return <Pending />;
  if (!isLoggedIn) {
    return !serverError ? <LoginPage /> : <ErrorPage />;
  }

  return (
    <div className={classes.root}>
      <Grid container>
        <Grid item xs={12}>
          <Navigation />
        </Grid>

        <Grid item xs={12} className={classes.standout}>
          <InfoCard platform="total" downloads={data.total} />
        </Grid>

        <Grid item xs={12}>
          <InfoCard
            platform="Android"
            downloads={data.Android}
          />
        </Grid>

        <Grid item xs={12}>
          <InfoCard platform="iOS" downloads={data.iOS} />
        </Grid>

        <Grid item xs={12}>
          {!serverError ? <FilterButtons /> : <ServerAlert />}
        </Grid>

        <Grid item xs={12} className={classes.block}>
          <LineChart
            months={data.months}
            downloadsByMonths={data.downloadsByMonths}
          />
        </Grid>

        <Grid item xs={12} className={classes.block}>
          <GroupedBar
            months={data.months}
            downloadsByMonths={data.downloadsByMonths}
          />
        </Grid>

        <Grid item xs={12} className={classes.standout}>
          <PieChart downloads={[data.Android, data.iOS]} />
        </Grid>
      </Grid>
      <br />
    </div>
  );
}

export default App;
