import Ajv from "ajv";

function DataValidator(data) {
  const ajv = new Ajv();
  const schema = require("./schemas/DownloadsDataSchema.json");
  const validate = ajv.compile(schema);
  for (const year in data) {
    if (Object.hasOwnProperty.call(data, year)) {
      if (!validate(data[year])) {
        throw new Error("Amplitude Update Error!\nTry again later");
      }
    }
  }
}

export default DataValidator;
